import { start } from "chromedriver";
import Vue from "vue";
import Vuex from 'vuex'

Vue.use(Vuex)


export default new Vuex.Store({
    state: {
        user: {
            //localStorage：本地存储 
            // 在项目打开的时候会判断本地存储中是否有user这个对象存在，如果有就取出来并取得username的值
            username: window.localStorage.getItem('user' || '[]') == null
                ? '' : JSON.parse(window.localStorage.getItem('user' || '[]')).username
        },
        sum: 0,
        blogId: 0
    },
    mutations: {
        login(state, user) {
            state.user = user
            window.localStorage.setItem('user', JSON.stringify(user))
        },
        logout(state) {
            state.user = []
            window.localStorage.removeItem('user')
        },
        JIA(state,value){
            state.sum += value
        },
        JIAN(state,value){
            state.sum -=value
        },
        JIJIA(state,value){
            state.sum +=value
        }


    },
    actions:{
        // jia(context,value){
        //     console.log('actions jia被调用');
        //     context.commit('JIA',value)
        // },
        // jian(context,value){
        //     console.log('actions jian被调用');
        //     context.commit('JIAN',value)
        // },
        jiaodd(context,value){
            if(context.state.sum % 2)
            context.commit('JIA',value)
        }

    },
    
})


