<template>
  <el-menu
    :default-active="$route.path"
    router
    mode="horizontal"
    background-color="white"
    text-color="#222"
    active-text-color="red"
    style="min-width: 1300px"
  >
    <el-menu-item v-for="(item, i) in navList" :key="i" :index="item.name">
      <!-- 以数组的名字作为导航 -->
      {{ item.navItem }}
    </el-menu-item>
    <!-- <a href="#nowhere" style="color: #222; float: right; padding: 20px"
      >{{username}}
    </a> -->

    <el-dropdown style="color: blue; float: right; padding: 20px">
      <span class="el-dropdown-link">
        {{ username }}<i class="el-icon-arrow-down el-icon--right"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item icon="el-icon-plus">个人中心</el-dropdown-item>
        <el-dropdown-item icon="el-icon-circle-plus" @click="logout"
          >退出登录</el-dropdown-item
        >
      </el-dropdown-menu>
    </el-dropdown>

    <!-- 图标 -->
    <i
      class="el-icon-menu"
      style="float: right; font-size: 45px; color: #222; padding-top: 8px"
    ></i>
    <span
      style="
        position: absolute;
        padding-top: 20px;
        right: 43%;
        font-size: 20px;
        font-weight: bold;
      "
      >White Jotter - Your Mind Palace</span
    >
  </el-menu>
</template>

<script>
export default {
  name: "NavMenu",
  data() {
    return {
      navList: [
        // { name: "/index", navItem: "首页" },
        { name: "/library", navItem: "图书馆" },
        { name: "/jotter", navItem: "笔记本" },
        { name: "/index", navItem: "练习" },
        // { name: "/index", navItem: "做着玩的"}
      ],
      //该次登录用户名称
      username: JSON.parse(window.localStorage.getItem("user" || "[]"))
        .username,
    };
  },
  methods: {
    logout() {
      var _this = this;
      this.$axios.get("/logout").then((resp) => {
        if (resp.data.code === 200) {
          // 前后端状态保持一致
          _this.$store.commit("logout");
          _this.$router.replace("/login");
        }
      });
    },
  },
};
</script>

<style scoped>
a {
  text-decoration: none;
}

/* span {
  pointer-events: none;
} */
</style>




