<template>
  <!-- 主页的父组件 -->
  <div>
    <NavMenu></NavMenu>
    <keep-alive include="JotterIndex">
    <router-view />
    </keep-alive>
  </div>
</template>

<script>
import NavMenu from "./common/NavMenu.vue";
export default {
  components: { NavMenu },
  name: "Home",
};
</script>

<style scoped>
/* scoped 样式私有化，不能让当前组件的样式修改影响到其他任何地方的样式 */

</style>


