import Vue from 'vue'
import Router from 'vue-router'
// 导入刚才编写的组件
// import AppIndex from '@/components/home/AppIndex'
// import Login from '@/components/Login'
import Home from '../components/Home'



Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    // 下面都是固定的写法
    {
      path: '/home',
      name: 'Home',
      component: Home,
      redirect: '/library',
      // redirect 重定向路由
      children: [
        {
          path: '/index',
          name: 'AppIndex',
          component: () =>import('../components/home/AppIndex'),
          meta: {
            requireAuth: true
          },
          children: [
            {
              path: '/value',
              name: 'value',
              component: () =>import('../components/common/value'),
            }
          ]
        },
        {
          path: '/library',
          name: 'Library',
          component: () =>import('../components/library/LibraryIndex'),
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/jotter',
          name: 'JotterIndex',
          component: () =>import('../components/jotter/JotterIndex'),
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/blog',
          name: 'blog',
          component: () =>import('../components/jotter/Blog'),
          meta:{
            requireAuth: true
          }
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: () =>import('@/components/Login')
    },
    {
      path:'/',
      name: 'Home',
      component: Home,
      redirect: '/library'
    },
    {
      path:'',
      name: 'Home',
      component: Home,
      redirect: '/library'
    }
  ]
})


